    url('../../fonts/suisse/intl/bold/suissebpintl-bold-webfont.eot?#iefix') format('embedded-opentype'),
    url('../../fonts/suisse/intl/bold/suissebpintl-bold-webfont.woff2') format('woff2'),
    url('../../fonts/suisse/intl/bold/suissebpintl-bold-webfont.woff') format('woff'),
    url('../../fonts/suisse/intl/bold/suissebpintl-bold-webfont.ttf')  format('truetype'),
    url('../../fonts/suisse/intl/bold/suissebpintl-bold-webfont.svg#svgFontName') format('svg');
  font-weight: 600;
  font-style: normal;
}

